import {Component, Injectable, OnInit} from '@angular/core';
import {OptionGroupService} from './service/option-group.service';
import {OptionGroup} from './model/optiongroup.model';
import {Event, NavigationEnd, NavigationStart, Router} from '@angular/router'
import {NgxSpinnerService} from 'ngx-spinner'
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Rx';
import {map, startWith, switchMap} from 'rxjs/operators';

@Component({
    selector: 'app-tbl-option-group',
    templateUrl: './tbl-option-group.component.html',
    styleUrls: ['./tbl-option-group.component.scss'],
})
@Injectable()
export class TblOptionGroupComponent implements OnInit {
    public optionGroups: OptionGroup[] = [];
    public optionGroup: OptionGroup = new OptionGroup({});
    searchTerm: FormControl = new FormControl();
    public p: number;
    public total: number = this.Count('');
    public name = '';
    public code: string;
    public url: string;
    private result: OptionGroup[];
    filteredOptions: OptionGroup[] = [];
    filteredOption: OptionGroup = new OptionGroup({});
    public dis = true;
    public minLength = 1;
    public checked = true;
    showLoading = true;
    constructor(public optionGroupService: OptionGroupService, public router: Router,
                private spinner: NgxSpinnerService,
    ) {
    }
    ngOnInit() {
        this.LoadData(1);
        this.searchTerm.setValue('');
        this.searchTerm.valueChanges.subscribe(
            term => {
                if (term !== '') {
                    this.optionGroupService.Search1(term).subscribe(
                        data => {
                            this.filteredOptions = data;
                        })
                } else {
                    this.filteredOptions = [];
                }
            });
    }

    Spinner() {
        this.spinner.show();
        setTimeout(() => {
            this.spinner.hide();
        }, 300);
    }


    Search() {
        if (this.name !== undefined) {
            this.p = 1;
            this.total = this.Count(this.name);
            this.optionGroupService.Search(1, this.name).subscribe(data => {
                this.optionGroups = data;
            })
        }
    }

    Clear() {
        this.code = '';
        this.name = '';
        this.Search();
    }

    LoadData(page) {
        if (this.code === undefined) {
            this.total = this.Count('');
            this.optionGroupService.Get(page).subscribe(data => {
                this.optionGroups = data;
                this.p = page;
            })
        } else {
            this.total = this.Count(this.code);
            this.optionGroupService.Search(page, this.code).subscribe(data => {
                this.optionGroups = data;
                this.p = page;
            })
        }
    }

    Count(ma): number {
        this.optionGroupService.Count(ma).subscribe(data => {
            this.total = +data;
        });
        return this.total;
    }

    Count1(ten): number {
        this.optionGroupService.Count(ten).subscribe(data => {
            this.total = +data;
        });
        return this.total;
    }


    DeleteItem(id: number) {
        const x = confirm('bn c tht s mun xa d liu ny ???');
        if (x === true) {
            this.p = 1;
            this.total = this.Count('');
            this.optionGroupService.Delete(id).subscribe(data => {
                this.Spinner();
                const index = this.GetIndex(data);
                this.optionGroups.splice(index, 1);
                this.LoadData(this.p);
            });
        } else {
            this.Spinner();
            this.LoadData(1);
        }
    }

    GetIndex(id: number): number {
        let index = -1;
        this.optionGroups.forEach((item, idx) => {
            if (item.id === id) {
                index = idx;
                return;
            }
        });
        return index;
    }
}
