import { Pipe, PipeTransform } from '@angular/core';
import { Menu } from './menu/model/menu.model';
import {MenuService} from './menu/service/menu.service';
import { ActivatedRoute } from '@angular/router'
@Pipe({
  name: 'filter2'
})
export class FilterPipe implements PipeTransform {
  id1: number;
  constructor(public route: ActivatedRoute, public menuService: MenuService) {

  }
  transform(menus: Menu[], term: string): Menu[] {
    if (term === undefined) { return menus; }
    return menus.filter(function (item) {
        return (item.menuCha.filter(null));
    })
  }

}
