import {Component, OnInit} from '@angular/core';
import {OptionGroup} from '../tbl-option-group/model/optiongroup.model';
import {Location} from '@angular/common';
import {OptionGroupService} from '../tbl-option-group/service/option-group.service';
import {ActivatedRoute, Router} from '@angular/router';
import {NgxSpinnerService} from 'ngx-spinner';
import {Subscription} from 'rxjs/Rx';

@Component({
    selector: 'app-child-detail-option-component',
    templateUrl: './child-detail-option-component.component.html',
    styleUrls: ['./child-detail-option-component.component.scss']
})
export class ChildDetailOptionComponentComponent implements OnInit {
    public childDetailOption: OptionGroup = new OptionGroup({});
    public childDetailOptions: OptionGroup[] = [];
    public id: number;
    private idSubscribe: Subscription;
    public parentId: number;
    constructor(private location: Location, private optionGroupService: OptionGroupService,
                private activatedroute: ActivatedRoute, public router: Router) {
    }

    ngOnInit() {
        this.idSubscribe = this.activatedroute.parent.params.subscribe(params => {
            this.parentId = +params['id'];
        });
        try  {
            // console.log(this.activatedroute.parent.snapshot.params['id']);

        } catch (e) {
            console.log(e)
        }

        this.getId();
        // console.log(this.parentId);
        // console.log(this.id)
    }
    getId() {
        this.id = +this.activatedroute.snapshot.paramMap.get('id');
        if (this.id !== 0) {
            this.optionGroupService.getIdChild(this.id).subscribe(data => {
                this.childDetailOption = data;
            })
        }
    }
    Back() {
        this.router.navigate([`/tbl-optiongroup/detail/${this.parentId}/option`])
    }

    Next() {
        this.router.navigate([`/tbl-optiongroup/detail/${this.parentId}/option/detail/${this.id}/test`])
    }

    saveEdit() {
        const x = confirm('bn c mun lu chnh sa');
        if (x === true) {
            this.optionGroupService.putChildOption(this.childDetailOption).subscribe(data => {
                const index = this.getIndex(this.id);
                this.childDetailOption[index] = data;
                this.Back();
            })
        } else {
            this.ngOnInit();
        }
    }
    getIndex(id: number): number {
        let index = -1;
        this.childDetailOptions.forEach((item, idx) => {
            if (item.id === id) {
                index = idx;
                return;
            }
        });
        return index;
    }
}
