import { Component, OnInit, OnDestroy } from '@angular/core';
import { StudentService } from './service/student.service'
import { Subscription } from 'rxjs/Subscription'
import { Student } from './model/student.class'
import { Observable } from 'rxjs';
@Component({
  selector: 'app-http',
  templateUrl: './http.component.html',
  styleUrls: ['./http.component.scss']
})
export class HttpComponent implements OnInit, OnDestroy {
  public sub: Subscription;
  public sv: Student[] = [];
  public MSSV: number;
  public HoTen: string;
  public HocLuc: string;
  public HanhKiem: string;
  public sinhVien: Student = null;
  constructor(public myService: StudentService) { }

  ngOnInit() {
    this.LoadData()
  }
  LoadData() {
    this.sub = this.myService.Get().subscribe(data => {
      console.log(data);
      this.sv = data;
    }, error => {
      console.log(error);
      this.myService.handleError(error);
    });
  }
  ngOnDestroy(): void {
    if (this.sub)
      this.sub.unsubscribe();
  }
  AddStudent() {
    let sv = new Student(this.MSSV, this.HoTen, this.HocLuc, this.HanhKiem);
    this.sub = this.myService.Post(sv).subscribe(data => {
      this.sv.push(data);
    })
  }
  Edit(sinhVien: Student) {
    this.sinhVien = sinhVien;
  }
  Update() {
    this.sub = this.myService.Put(this.sinhVien).subscribe(data => {
      let index=this.getIndex(data.id);
      this.sv[index]=data;
    }, error => {
      console.log(error);
      this.myService.handleError(error);
    })
  }
  Delete(id: number) {
    this.sub = this.myService.Delete(id).subscribe(data => {
      let index=this.getIndex(data.id);
      this.sv.splice(index,1);
    }, error => {
      console.log(error);
      this.myService.handleError(error);
    })
  }
  getIndex(id:number):number{
    let result=0;
    this.sv.forEach((Student,index)=>{
      if(Student.id==id){
        result=index
      }
    })
    return result;
  }
}
