import {Component, OnInit} from '@angular/core';
import {Option} from '../../model/option.model'
import {OptionService} from '../../service/option.service'
import {Subscription} from 'rxjs/Subscription';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common'

@Component({
    selector: 'app-option',
    templateUrl: './option.component.html',
    styleUrls: ['./option.component.scss']
})
export class OptionComponent implements OnInit {
    public options: Option[] = [];
    public sub: Subscription;
    public p: number;
    public Option: Option = new Option({});

    constructor(private location: Location, public router: Router, public route: ActivatedRoute, public optionService: OptionService) {
    }

    ngOnInit() {
        this.LoadData(1);
        const id = +this.route.snapshot.paramMap.get('id');
        if (id !== 0) {
            this.GetId();
        }
        // else{
        //   this.LoadData();
        // }
    }

    GetId(): void {
        const id = +this.route.snapshot.paramMap.get('id');
        this.optionService.GetId(id).subscribe(data => {
            this.Option = data;
        })
    }

    LoadData(page) {
        this.optionService.Get(page).subscribe(data => {
            this.options = data;
            this.p = page;
        })
    }

    Add() {
        this.optionService.Post(this.Option).subscribe(data => {
            this.options.push(data);
        })
    }

    BackUrl() {
        this.router.navigate(['/tbl-option']);
    }

    Back() {
        this.location.back();
    }

    Save(): void {
        let id = +this.route.snapshot.paramMap.get('id');
        if (id !== 0) {
            this.sub = this.optionService.Put(this.Option).subscribe(data => {
                let index = this.getIndex(data.id);
                this.options[index] = data;
                this.Option = new Option({});
                this.BackUrl();
            })
        }
        else {
            this.sub = this.optionService.Post(this.Option).subscribe(data => {
                this.options.push(data);
                this.Option = new Option({});
                this.BackUrl();
            })
        }
    }

    Delete(id: number) { //delete item
        this.optionService.Delete(id).subscribe(data => {
            let index = this.getIndex(data);
            this.options.splice(index, 1);
            this.BackUrl();
        })
    }

    Edit(item: Option) {
        this.Option = item;
    }

    navigate(url: string) {
        console.log(url);
    }

    getIndex(id: number): number {
        let index = -1;
        this.options.forEach((item, idx) => {
            if (item.id == id) {
                index = idx;
                return;
            }
        });
        return index;
    }

    goBack(): void {
        this.location.forward();
    }
}
