import {Component, OnInit} from '@angular/core';
import {Location} from '@angular/common';
import {OptionGroupService} from '../tbl-option-group/service/option-group.service';
import {OptionGroup} from '../tbl-option-group/model/optiongroup.model'
import {ActivatedRoute, Router} from '@angular/router'
import {NgxSpinnerService} from 'ngx-spinner';

@Component({
    selector: 'app-tbl-option',
    templateUrl: './tbl-option.component.html',
    styleUrls: ['./tbl-option.component.scss'],
})
export class TblOptionComponent implements OnInit {
    public optionGroup: OptionGroup = new OptionGroup({});
    public optionGroups: OptionGroup[] = [];
    public p: number;
    public id: number;
    public name = '';

    constructor(private location: Location, private optionGroupService: OptionGroupService,
                private activatedroute: ActivatedRoute, public router: Router, private spinner: NgxSpinnerService) {
    }

    ngOnInit() {
        this.Spinner();
        this.getId();
       // console.log(this.id);
    }

    Spinner() {
        this.spinner.show();
        setTimeout(() => {
            this.spinner.hide();
        }, 300);
    }

    Back() {
        this.router.navigate(['/tbl-optiongroup'])
    }

    getId() {
        this.id = +this.activatedroute.snapshot.paramMap.get('id');
        if (this.id !== 0) {
            this.optionGroupService.getId(this.id).subscribe(data => {
                this.optionGroup = data;
            })
        }
    }

    saveEdit() {
        const x = confirm('bn c mun lu chnh sa');
        if (x === true) {
            this.optionGroupService.Put(this.optionGroup).subscribe(data => {
                const index = this.getIndex(this.id);
                this.optionGroup[index] = data;
                this.Back();
            })
        } else {
            this.ngOnInit();
        }
    }

    Clear() {
        this.optionGroup.code = '';
        this.optionGroup.name = '';
    }

    getIndex(id: number): number {
        let index = -1;
        this.optionGroups.forEach((item, idx) => {
            if (item.id === id) {
                index = idx;
                return;
            }
        });
        return index;
    }

    Save() {
        const x = confirm('bn c mun lu');
        if (this.optionGroup.code === undefined || this.optionGroup.name === undefined) {
            alert('thng tin khng c thiu');
        } else {
            if (x === true) {
                this.optionGroupService.Post(this.optionGroup).subscribe(data => {
                    this.optionGroups.push(data);
                    alert('save thnh cng');
                    this.Clear();
                    this.Spinner();
                })
            } else {
                this.ngOnInit();
            }
        }
    }
}
