import {Component, OnInit} from '@angular/core';
import {OptionGroup} from '../tbl-option-group/model/optiongroup.model';
import {FormControl} from '@angular/forms';
import {OptionGroupService} from '../tbl-option-group/service/option-group.service';
import {Router} from '@angular/router';
import {NgxSpinnerService} from 'ngx-spinner';
import {Location} from '@angular/common';

@Component({
    selector: 'app-test-modal',
    templateUrl: './test-modal.component.html',
    styleUrls: ['./test-modal.component.scss']
})
export class TestModalComponent implements OnInit {
    public optionGroups: OptionGroup[] = [];
    public optionGroup: OptionGroup = new OptionGroup({});
    searchTerm: FormControl = new FormControl();
    public p: number;
    public total: number = this.Count('');
    public name = '';
    public code: string;
    public url: string;
    filteredOptions: OptionGroup[] = [];
    isOpen = false;

    constructor(public optionGroupService: OptionGroupService, public router: Router,
                private spinner: NgxSpinnerService, private location: Location
    ) {
    }

    ngOnInit() {
        this.Spinner();
        this.LoadData(1);
        this.searchTerm.setValue('');
        this.searchTerm.valueChanges.subscribe(
            term => {
                if (term !== '') {
                    this.optionGroupService.Search1(term).subscribe(
                        data => {
                            this.filteredOptions = data;
                        })
                } else {
                    this.filteredOptions = [];
                }
            });
    }

    openModal() {
        this.isOpen = !this.isOpen
    }

    hide() {
        this.isOpen = false;
    }

    Spinner() {
        this.spinner.show();
        setTimeout(() => {
            this.spinner.hide();
        }, 300);
    }


    Search() {
        if (this.code !== undefined) {
            this.p = 1;
            this.total = this.Count(this.code);
            this.optionGroupService.Search(1, this.code).subscribe(data => {
                this.optionGroups = data;
            })
        }
    }

    Clear() {
        this.code = '';
        this.name = '';
        this.Search();
    }

    LoadData(page) {
        if (this.code === undefined) {
            this.total = this.Count('');
            this.optionGroupService.Get(page).subscribe(data => {
                this.optionGroups = data;
                this.p = page;
            })
        } else {
            this.total = this.Count(this.code);
            this.optionGroupService.Search(page, this.code).subscribe(data => {
                this.optionGroups = data;
                this.p = page;
            })
        }
    }

    Count(ma): number {
        this.optionGroupService.Count(ma).subscribe(data => {
            this.total = +data;
        });
        return this.total;
    }

    Count1(ten): number {
        this.optionGroupService.Count(ten).subscribe(data => {
            this.total = +data;
        });
        return this.total;
    }


    DeleteItem(id: number) {
        const x = confirm('bn c tht s mun xa d liu ny ???');
        if (x === true) {
            this.p = 1;
            this.total = this.Count('');
            this.optionGroupService.Delete(id).subscribe(data => {
                this.Spinner();
                const index = this.GetIndex(data);
                this.optionGroups.splice(index, 1);
                this.LoadData(this.p);
            });
        } else {
            this.Spinner();
            this.LoadData(1);
        }
    }

    GetIndex(id: number): number {
        let index = -1;
        this.optionGroups.forEach((item, idx) => {
            if (item.id === id) {
                index = idx;
                return;
            }
        });
        return index;
    }
}
