import {Component, OnInit, Input} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Menu} from '../../model/menu.model'
import {MenuService} from '../../service/menu.service'
import {Location} from '@angular/common'
import {Subscription, Observable} from 'rxjs';

@Component({
    selector: 'app-menu-detail',
    templateUrl: './menu-detail.component.html',
    styleUrls: ['./menu-detail.component.scss']
})
export class MenuDetailComponent implements OnInit {
    public count = 'http://42.117.5.115:8049/coreAPI/tbl_menu/find?pageSize=10&page=1'
    public sub: Subscription;
    public Menu: Menu = new Menu({});
    public menuParent: Menu = new Menu({});
    public menu: Menu = null;
    public menus: Menu[] = [];
    public menuParents: Menu[] = [];
    public p: number;
    private result: Menu[];

    constructor(private routerService: Router
                , private route: ActivatedRoute
                , public menuService: MenuService, public activatedRouter: ActivatedRoute, private location: Location) {
    }

    ngOnInit() {
        this.getMenuById();
        this.Get(0);
    }

    getMenuById(): void {
        const id = +this.route.snapshot.paramMap.get('id');
        if (id !== 0) {
            this.menuService.GetId(id).subscribe(data => {
                this.Menu = data;
            })
        }
    }

    Get(page) {
        this.menuService.Get1(page).subscribe(data => {
            this.menus = data;
            this.p = page;
        })
    }

    Add(): void {
        this.menuService.Post(this.Menu).subscribe(data => {
            this.menus.push(data);
        })
    }

    goBack(): void {
        this.location.back();
    }

    Save(): void {
        const id = +this.route.snapshot.paramMap.get('id');
        if (id === 0) {
            this.menuService.Post(this.Menu).subscribe(data => {
                this.menus.push(data);
            })
            this.goBack();
        } else {
            this.sub = this.menuService.Put(this.Menu).subscribe(data => {
                const index = this.getIndex(data.id);
                this.Menu[index] = data;
            })
            this.goBack();
        }
    }

    Update() {
        this.sub = this.menuService.Put(this.Menu).subscribe(data => {
            const index = this.getIndex(data.id);
            this.Menu[index] = data;
        })
    }

    getIndex(id: number): number {
        let index = -1;
        this.menus.forEach((item, idx) => {
            if (item.id == id) {
                index = idx;
                return;
            }
        });
        return index;
    }
}
