import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterabc'
})
export class FilterPipeABC implements PipeTransform {

  transform(options: any, term: any): any {
    if(term===undefined) return options;
    return options.filter(function(item){
      return (item.ten.toLowerCase().includes(term.toLowerCase()));
    })
  }

}
