import {Routes} from '@angular/router';

import {DashboardComponent} from '../../dashboard/dashboard.component';
import {UserProfileComponent} from '../../user-profile/user-profile.component';
import {TableListComponent} from '../../table-list/table-list.component';
import {TypographyComponent} from '../../typography/typography.component';
import {IconsComponent} from '../../icons/icons.component';
import {MapsComponent} from '../../maps/maps.component';
import {NotificationsComponent} from '../../notifications/notifications.component';
import {UpgradeComponent} from '../../upgrade/upgrade.component';
import {HttpComponent} from '../../http/http.component';
import {QlsvComponent} from '../../qlsv/qlsv.component';
import {QuanliComponent} from '../../quanli/quanli.component'
import {MenuComponent} from './../../menu/menu.component'
import {MenuDetailComponent} from 'app/menu/component/menu-detail/menu-detail.component';
import {TblOptionComponent} from 'app/tbl-option/tbl-option.component';
import {TblOptionGroupComponent} from 'app/tbl-option-group/tbl-option-group.component';
import {ChildOptionComponent} from '../../tbl-option/component/child-option/child-option.component';
import {ChildDetailOptionComponentComponent} from '../../child-detail-option-component/child-detail-option-component.component';
import {TestChildComponent} from '../../test-child/test-child.component'
import {TestModalComponent} from '../../test-modal/test-modal.component';
import {TestModalDetailComponent} from '../../test-modal-detail/test-modal-detail.component';

export const AdminLayoutRoutes: Routes = [
    {path: 'dashboard', component: DashboardComponent},
    {path: 'user-profile', component: UserProfileComponent},
    {path: 'table-list', component: TableListComponent},
    {path: 'typography', component: TypographyComponent},
    {path: 'icons', component: IconsComponent},
    {path: 'maps', component: MapsComponent},
    {path: 'notifications', component: NotificationsComponent},
    {path: 'upgrade', component: UpgradeComponent},
    {path: 'http', component: HttpComponent},
    {path: 'qlsv', component: QlsvComponent},
    {
        path: 'modal',
        component: TestModalComponent
    },
    {
        path: 'quanli',
        component: QuanliComponent,
    },
    {
        path: 'menu',
        component: MenuComponent,
    },
    {
        path: 'menu/detail/:id',
        component: MenuDetailComponent
    },

    {
        path: 'menu/detail',
        component: MenuDetailComponent,
    },
    {
        path: 'tbl-optiongroup',
        component: TblOptionGroupComponent
    },
    {
        path: 'tbl-optiongroup/detail',
        component: TblOptionComponent
    },
    {
        path: 'tbl-optiongroup/detail/:id',
        component: TblOptionComponent,
        children: [{
            path: 'option',
            component: ChildOptionComponent,
        }, {
            path: 'option/detail/:id',
            component: ChildDetailOptionComponentComponent,
            children: [{
                path: 'test',
                component: TestChildComponent,
            }],
        }]
    }
];
