import {Component, OnInit, OnDestroy} from '@angular/core';
import {QuanLiModel} from './model/quanli.model';
import {QuanliService} from './service/quanli.service';
import {Subscription} from 'rxjs/Subscription'
import {OrderPipe} from 'ngx-order-pipe';
import {Router} from '@angular/router'

@Component({
    selector: 'app-quanli',
    templateUrl: './quanli.component.html',
    styleUrls: ['./quanli.component.scss']
})
export class QuanliComponent implements OnInit, OnDestroy {
    public sub: Subscription;
    public quanli: QuanLiModel[] = [];
    public order: string = 'id';
    public reverse: boolean = false;
    selectedQuanli: QuanLiModel;
    public url: string;
    public ten: string;
    public menuCha: any[];
    public hinh: string;
    public menuID: string;
    public nhom: number;
    public stt: number;
    public lvl: number;

    onSelect(ql: QuanLiModel): void {
        this.selectedQuanli = ql;
    }

    public Quanli: QuanLiModel = new QuanLiModel({});
    sortedCollection: QuanLiModel[];

    constructor(public myService: QuanliService, private orderPipe: OrderPipe, public routerService: Router) {
        this.sortedCollection = orderPipe.transform(this.quanli, this.order);
        console.log(this.sortedCollection);
    }

    ngOnInit() {
        this.LoadData();
    }

    ngOnDestroy(): void {
        if (this.sub)
            this.sub.unsubscribe();
    }

    navigate(url: QuanLiModel) {
        this.routerService.navigate([url]);
    }

    setOrder(value: string) {
        if (this.order === value) {
            this.reverse = !this.reverse;
        }
        this.order = value;
    }

    LoadData() {
        this.sub = this.myService.Get().subscribe(data => {
            console.log(data);
            this.quanli = data;
        })
    }

    Add() {
        // let ql = new QuanLis(this.url, this.ten, this.menuCha, this.hinh, this.menuID, this.nhom, this.stt, this.lvl);
        this.myService.Post(this.Quanli).subscribe(data => {
            this.quanli.push(data); //thm vo cui
        })
    }

    Update() {
        this.sub = this.myService.Put(this.Quanli).subscribe(data => {
            const index = this.getIndex(data.id);
            this.quanli[index] = data;
        })
    }

    Delete(id: number) { //delete item
        this.sub = this.myService.Delete(id).subscribe(data => {
            const index = this.getIndex(data);
            this.quanli.splice(index, 1);
        })
    }

    Edit(item: QuanLiModel) {
        this.Quanli = item;
    }

    getIndex(id: number): number {
        let index = -1;
        this.quanli.forEach((item, idx) => {
            if (item.id == id) {
                index = idx;
                return;
            }
        });
        return index;
    }
}
