import {Component, OnDestroy, OnInit, Input, ViewChild} from '@angular/core';
import {Location} from '@angular/common';
import {OptionGroupService} from '../../../tbl-option-group/service/option-group.service';
import {OptionGroup} from '../../../tbl-option-group/model/optiongroup.model'
import {ActivatedRoute, ParamMap, Router} from '@angular/router'
import {Subscription} from 'rxjs/Rx';

@Component({
    selector: 'app-child-option',
    templateUrl: './child-option.component.html',
    styleUrls: ['./child-option.component.scss']
})
export class ChildOptionComponent implements OnInit, OnDestroy {
    private idSubscribe: any;
    public childOption: OptionGroup = new OptionGroup({});
    public childOptionAdd: OptionGroup = new OptionGroup({});
    public childOptions: OptionGroup[] = [];
    public p: number;
    public parentId: number;
    public id: number;
    public name = '';
    public total: number = this.countItems('');

    constructor(private routerService: Router,
                private location: Location, private optionGroupService: OptionGroupService,
                private activatedroute: ActivatedRoute) {
    }

    ngOnInit() {
        this.idSubscribe = this.activatedroute.parent.params.subscribe(params => {
            this.parentId = +params['id'];
        });
        this.loadData(1, this.parentId);
    }

    ngOnDestroy() {
        this.idSubscribe.unsubscribe();
    }

    countItems(name): number {
        this.optionGroupService.countOption(this.parentId, name).subscribe(data => {
            this.total = +data;
        });
        return this.total;
    }

    Back() {
        this.childOption = new OptionGroup({});
    }

    editVal(val) {
        this.childOption = val;
    }

    loadData(page, parentId) {
        if (this.name === '') {
            this.total = this.countItems('');
            this.optionGroupService.getOption(page, parentId).subscribe(data => {
                this.childOptions = data;
                this.p = page;
            });
        } else {
            this.total = this.countItems(this.name);
            this.optionGroupService.searchOption(page, this.name, parentId).subscribe(data => {
                this.childOptions = data;
                this.p = page;
            })
        }
    }

    clearInputAdd() {
        this.childOptionAdd.code = '';
        this.childOptionAdd.name = '';
        this.childOptionAdd.val = '';
        this.childOptionAdd.orderNo = '';
        this.childOptionAdd.icon = '';
    }

    searchName() {
        if (this.name !== undefined) {
            this.p = 1;
            this.total = this.countItems(this.name);
            this.optionGroupService.searchOption(1, this.name, this.parentId).subscribe(data => {
                this.childOptions = data;
            })
        } else {
            this.loadData(1, this.parentId);
        }
    }

    saveAdd() {
        const x = confirm('bn c tht s mun lu d liu ny ???');
        const param = {
            ...this.childOptionAdd,
            optionGroupId: {
                id: this.parentId
            },
        };
        if (x === true) {
            this.optionGroupService.postChildOption(param).subscribe(data => {
                this.childOptions.push(data);
                this.loadData(1, this.parentId);
                this.clearInputAdd();
            })
        } else {
            this.loadData(1, this.parentId);
        }

    }

    deleteItem(id: number) {
        const x = confirm('bn c tht s mun xa d liu ny ???');
        if (x === true) {
            this.optionGroupService.deleteChildOption(id).subscribe(data => {
                const index = this.getIndex(data);
                this.childOptions.splice(index, 1);
                this.loadData(1, this.parentId);
            })
        } else {
            this.loadData(1, this.parentId);
        }
    }


    saveEdit() {
        const x = confirm('bn c mun lu chnh sa');
        if (x === true) {
            this.optionGroupService.putChildOption(this.childOption).subscribe(data => {
                const index = this.getIndex(this.parentId);
                this.childOption[index] = data;
                this.Back();
            });
            this.Back();
        } else {
            this.ngOnInit();
            this.clearInputAdd();
        }
    }

    getIndex(id: number): number {
        let index = -1;
        this.childOptions.forEach((item, idx) => {
            if (item.id === id) {
                index = idx;
                return;
            }
        });
        return index;
    }
}
