import {Component, OnInit, Input, OnDestroy} from '@angular/core';
import {Menu} from '../menu/model/menu.model'
import {MenuService} from '../menu/service/menu.service'
import {Router} from '@angular/router'
import {ActivatedRoute} from '@angular/router'
import {OrderPipe} from 'ngx-order-pipe'
import {Subscription, Observable, Subject} from 'rxjs';
import {debounceTime, distinctUntilChanged, switchMap} from 'rxjs/operators';

@Component({
    selector: 'app-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.scss'],
})
export class MenuComponent implements OnInit, OnDestroy {
    sortedColection: Menu[];
    public searchTerms = new Subject<string>();
    public sub: Subscription;
    public order: string;
    public p: number ;
    total: number = this.Count('');
    // public pageSize: number = 10;
    show: boolean = true;
    public menus: Menu[] = [];
    public reverse: boolean = false;
    public Menu: Menu = new Menu({});
    public item: string = '';
    public name: string;
    public page: number;
    public items: string;
    public loading: boolean;

    constructor(private orderPipe: OrderPipe, private activatedRoute: ActivatedRoute, public menuService: MenuService, public router: Router) {
        this.sortedColection = orderPipe.transform(this.menus, 'info.id');
    }

    ngOnInit() {
        this.GetPage(1);
    }

    GetPage(page) {
       if (this.name === undefined) {
           this.menuService.Get(page).subscribe(data => {
               this.menus = data;
               this.p = page;
               this.loading = false;
               console.log(data);
           })
       } else {
            this.menuService.SearchMenu(page, this.name).subscribe(data => {
                this.menus = data;
                this.p = page;
                console.log(data);
            })
        }
    }

    Refresh() {
    }

    Count(ten): number {
        this.menuService.Count(ten).subscribe(data => {
            this.total = +data;
        })
        return this.total;
    }

    Search() {
        this.p = 1;
        this.total = this.Count(this.name);
        this.menuService.SearchMenu(1, this.name).subscribe(data => {
            this.menus = data;
            console.log(data);
        })
    }

    // LoadData(): void {
    //   this.menuService.Get().subscribe(data => {
    //     console.log(data);
    //     this.menus = data;

    //   })
    // }
    DeleteMenu(id: number) { //delete item
        this.menuService.Delete(id).subscribe(data => {
            let index = this.getIndex(data);
            this.menus.splice(index, 1);
        })
    }

    setOrder(value: string) {
        if (this.order === value) {
            this.reverse = !this.reverse;
        }
        this.order = value;
    }

    // Add(){
    //   return this.Menu;
    // }
    // DeleteMenu(menu:Menu):void{
    //   this.menus=this.menus.filter(data =>data!==menu);
    //   this.menuService.Delete(menu).subscribe();
    // }
    navigate(url: string, item: Menu) {
        console.log(url);
        this.router.navigateByUrl(url);
        this.Menu = item;
    }

    getIndex(id: number): number {
        let index = -1;
        this.menus.forEach((item, idx) => {
            if (item.id == id) {
                index = idx;
                return;
            }
        });
        return index;
    }

    ngOnDestroy() {
        if (this.sub)
            this.sub.unsubscribe();
    }
}
